.home-banner {
	overflow  : hidden;
	background: $main-color-4;

	.banner-wrapper {
		overflow: hidden;
		padding : 0;
	}

	.box-banner {
		display : flex;
		position: relative;
		width   : 100%;

		figure {
			position: relative;
			width   : 100%;

			img {
				width     : 100%;
				height    : 940px;
				object-fit: cover;
			}

			figcaption {
				position : absolute;
				top      : 80%;
				transform: translateY(-80%);
				left     : 130px;
				z-index  : 1;

				.type-of-event {
					display      : flex;
					align-items  : center;
					font-size    : 20px;
					margin-bottom: 50px;

					h5 {
						position    : relative;
						margin-right: 120px;

						span {
							margin-right: 5px;
							color       : $main-color-1;
						}

						&::after {
							content      : '';
							position     : absolute;
							top          : 50%;
							right        : -110px;
							background   : #FFFFFF;
							width        : 100px;
							height       : 3px;
							border-radius: 50px;
						}
					}

					span {
						color: $main-color-1;
					}
				}

				.name-event {
					margin-bottom: 20px;

					h5 {
						font-family: 'Pangolin', cursive;
						font-size  : 50px;
					}
				}

				.decription-event {
					p {
						font-family   : 'Anton', sans-serif;
						letter-spacing: 2px;
						font-size     : 20px;
						margin-bottom : 10px;
					}
				}
			}
		}

		&:after {
			content   : '';
			position  : absolute;
			left      : 0;
			right     : 0;
			bottom    : 0;
			top       : 0;
			background: rgba(26, 29, 30, 0);
			background: -moz-linear-gradient(top, rgba(26, 29, 30, 0) 0%, rgba(26, 29, 30, 0.7) 42%, rgba(26, 29, 30, 0.8) 56%, rgba(26, 29, 30, 0.9) 68%, rgba(26, 29, 30, 1) 80%, rgba(26, 29, 30, 1) 100%);
			background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(26, 29, 30, 0)), color-stop(42%, rgba(26, 29, 30, 0.7)), color-stop(56%, rgba(26, 29, 30, 0.8)), color-stop(68%, rgba(26, 29, 30, 0.9)), color-stop(80%, rgba(26, 29, 30, 1)), color-stop(100%, rgba(26, 29, 30, 1)));
			background: -webkit-linear-gradient(top, rgba(26, 29, 30, 0) 0%, rgba(26, 29, 30, 0.7) 42%, rgba(26, 29, 30, 0.8) 56%, rgba(26, 29, 30, 0.9) 68%, rgba(26, 29, 30, 1) 80%, rgba(26, 29, 30, 1) 100%);
			background: -o-linear-gradient(top, rgba(26, 29, 30, 0) 0%, rgba(26, 29, 30, 0.7) 42%, rgba(26, 29, 30, 0.8) 56%, rgba(26, 29, 30, 0.9) 68%, rgba(26, 29, 30, 1) 80%, rgba(26, 29, 30, 1) 100%);
			background: -ms-linear-gradient(top, rgba(26, 29, 30, 0) 0%, rgba(26, 29, 30, 0.7) 42%, rgba(26, 29, 30, 0.8) 56%, rgba(26, 29, 30, 0.9) 68%, rgba(26, 29, 30, 1) 80%, rgba(26, 29, 30, 1) 100%);
			background: linear-gradient(to bottom, rgba(26, 29, 30, 0) 0%, rgba(26, 29, 30, 0.7) 42%, rgba(26, 29, 30, 0.8) 56%, rgba(26, 29, 30, 0.9) 68%, rgba(26, 29, 30, 1) 80%, rgba(26, 29, 30, 1) 100%);
			filter    : progid:DXImageTransform.Microsoft.gradient(startColorstr='#1a1d1e', endColorstr='#1a1d1e', GradientType=0);
		}
	}
}

.slider-Homebanner {
	.swiper-slide {
		.box-banner {
			figure {
				img {
					transition      : 1s all ease-in-out;
					transition-delay: 1s;
				}

				figcaption {
					transition      : 1s all ease-in-out;
					transition-delay: 1s;
					transform       : translateY(100%);
				}
			}

			&:after {
				transition      : 1s all ease-in-out;
				transition-delay: 1s;
			}
		}


		&.swiper-slide-active {
			.box-banner {

				figure {
					img {
						transform: scale(1.2);
					}

					figcaption {
						transform: translateY(-100%);
					}
				}

				&:after {
					transform: scale(1.2);
				}
			}
		}
	}
}